input {
  color:#f92672;
  background-color: #181818;
  border: none;
  border-bottom: 1px solid 		#2abed9;
}

input:hover {
  border: none;
  border-bottom: 1px solid 	#25d9c8;
  transition: all .3s;
}
input:focus {
  outline: none;
  border: none;
  border-bottom: 1px dashed 	#2abed9;
  transition: all .3s;
}